import React, {Component} from 'react';
import logo from './logo.svg';
import Badge from './containers/Uielements/Badge/badge.style';
import ContentHolder from './components/utility/contentHolder';
import 'antd/dist/antd.css';

import CookieReader from './CookieReader'

import './App.css';

class App extends Component {

    render = () => {
        return (
            <div className="App">
                <header className="App-header">
                    <h1>Welcome to Aurora!</h1>


                        <ContentHolder>
                            <Badge count={5}>
                            <a className="isoBadgeLink"> </a>
                            </Badge>
                        </ContentHolder>
                    <CookieReader saveCookie={this.props.saveCookie} loadCookie={this.props.loadCookie}/>
                </header>
            </div>
        );
    }
}

export default App;
